
.Form{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 600px;
    height: 300px;
    background-color: #40575a2d;
    margin-bottom: 10px;
    margin-top: 50px;
    padding-top: 100px;
    border-radius: 20px;
}

.Input{
    font-size: 22px;
    letter-spacing: 4px;
    width: 450px;
    height: 60px;
    margin-bottom: 30px;
    border-radius: 5px;
    padding-left: 10px;
    padding-right: 10px;
    border-style: none;
    background-color: rgb(53, 58, 63);
    color: aliceblue;
    box-shadow: rgba(0, 0, 0, 0.5) 0px 2px 4px, rgba(0, 0, 0, 0.4) 0px 7px 13px -3px, rgba(0, 0, 0, 0.3) 0px -3px 0px inset;
    text-shadow: 0 1px 0 #cac8c8,
    0 2px 0 #5e5858,
    0 3px 0 #363333,
    0 4px 0 #4b4545,
    0 5px 0 #272424,
    0 6px 1px rgba(0,0,0,.2),
    0 0 5px rgba(0,0,0,0.1),
    0 1px 3px rgba(0,0,0,.1),
    0 3px 5px rgba(0,0,0,.1),
    0 5px 10px rgba(0,0,0,.2),
    0 10px 10px rgba(0,0,0,.3),
    0 20px 20px rgba(0,0,0,.1);
    }


.FormBtn{
    width: 110px;
    height: 35px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 40px;
    background-color: rgb(29, 104, 209);
    color: aliceblue;
    border-style: none;
    border-radius: 5px;
    cursor: pointer;
   transition: 500ms cubic-bezier(0.075, 0.82, 0.165, 1);
    box-shadow: rgba(0, 0, 0, 0.5) 0px 2px 4px, rgba(0, 0, 0, 0.4) 0px 7px 13px -3px, rgba(0, 0, 0, 0.3) 0px -3px 0px inset;
    text-shadow: 0 1px 0 #cac8c8,
0 2px 0 #5e5858,
0 3px 0 #363333,
0 4px 0 #4b4545,
0 5px 0 #272424,
0 6px 1px rgba(0,0,0,.2),
0 0 5px rgba(0,0,0,0.1),
0 1px 3px rgba(0,0,0,.1),
0 3px 5px rgba(0,0,0,.1),
0 5px 10px rgba(0,0,0,.2),
0 10px 10px rgba(0,0,0,.3),
0 20px 20px rgba(0,0,0,.1);
}

.FormBtn:hover{
    background-color: rgb(13, 78, 169);
    transform: scale(1.1);
}
